<template>
  <div class="course-course-ware">
    <head-top title="课件">
    </head-top>

    <van-pull-refresh v-model="isLoading" :head-height="80" @refresh="getCourseWareList" class="pull_fresh">
      <van-cell-group v-if="fileList && fileList.length>0" class="cell_group">
        <van-cell class="cell_main" v-for="(item, index) in fileList" :key="index" @click="fileClick(item)">
          <template #icon>
            <van-icon class="cell_icon" name="orders-o"></van-icon>
          </template>
          <template #title>
            <div class="line_title">{{item.fileName + '.' + item.fileExt}}</div>
          </template>
          <template #label>
            <div class="line_title">
              <span v-if="item.fileSize > 1024 * 1024">
                {{Math.round(item.fileSize / 1024.0 / 10.24 ) / 100}} MB
              </span>
              <span v-else>
                {{Math.round(item.fileSize / 10.24 ) / 100}} KB
              </span>
            </div>
          </template>
          <template #right-icon>
            <van-icon v-if="item.isShare + '' === '1'" class-prefix="iconfont icon-zaixianzhihang"
                      :style="{color:'#07c160', fontWeight:'bolder'}"></van-icon>
            <van-icon v-else class-prefix="iconfont icon-xinxitongbu"></van-icon>
          </template>
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";

export default {
  name: "courseWare",
  data () {
    return {
      fileList: [],
      isLoading: false
    };
  },
  created () {
    this.getCourseWareList();
  },
  methods: {
    getCourseWareList () {
      this.fileList = [];
      this.isLoading = true;
      this.$mainHttp.courseware.inCourseList({
        chID: this.$store.getters.classHourID,
        classID: this.$store.getters.classID
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["IsSee"] > b["IsSee"] ? -1 : 1;
            });

            res.Datas.forEach(item => {
              this.fileList.push({
                id: item.Key["CID"],
                fileID: item.Key["MaterialID"],
                directoryID: item["DirectoryID"],
                fileName: item["MaterialName"],
                fileExt: item["MaterialFileExt"],
                fileSize: item["MaterialFileSize"] || 0,
                shareId: item["CClassID"],
                isShare: item["IsSync"],
                // fileTransformed: item["MaterialLowStreamEstate"],
                createTime: formatDate(item["MaterialCreateDate"], "yyyy-MM-dd")
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    fileClick (item) {
      Dialog.confirm({
        message: item.isShare + '' === '1' ? "确认取消同步文件？" : "确认同步文件?"
      }).then(() => {
        this.toShare(item);
      }).catch(() => {
      });
    },
    toShare (item) {
      const info = item.isShare ? "取消同步文件" : "同步文件";
      const isShare = item.isShare + '' === '1' ? '0' : '1';
      this.$mainHttp.courseware.setShare({
        ccID: item.shareId,
        isSync: isShare
      }).then(res => {
        if (res.Result) {
          this.getCourseWareList();
          this.$toast(info + "成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast(info + "失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-course-ware {
  min-height: 100%;
  background-color: #fff;

  .pull_fresh {
    height: 100%;
  }
}
</style>
